@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@300;700&family=Overpass:wght@400;700&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  font-family: "Overpass", sans-serif;
  background-color: #131518;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}
.App {
  width: 327px;
  height: 360px;
  border-radius: 15px;
  background-color: #232a34;
  padding: 24px;
}
h1 {
  font-weight: 700;
  color: white;
  margin-top: 28px;
  margin-bottom: 7px;
  font-size: 28px;
}
p {
  font-weight: 400;
  color: #969fad;
  font-size: 15px;
  margin-bottom: 16px;
  line-height: 22px;
}
.star-parent {
  display: flex;
  background-color: #262e38;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 20px;
}
.star {
  width: 14px;
  height: 14px;
}
@media screen and (min-width: 1440px) {
  .star {
    width: 16px;
    height: 16px;
  }
  .App {
    width: 412px;
    height: 416px;
  }
  .star-parent {
    margin-bottom: 30px;
    width: 48px;
    height: 48px;
  }
  p {
    margin-bottom: 30px;
  }
}
